___CSS_LOADER_EXPORT___.push([module.id, `.time-wrapper {
  padding: 0 15px;
  padding-top: 0px;
}
.time-wrapper .period {
  position: relative;
  display: flex;
  padding-top: 30px;
  margin-bottom: 5px;
}
.time-wrapper .period.color1 {
  background-color: #b5d6d6;
}
.time-wrapper .period.color2 {
  background-color: #ff7477;
}
.time-wrapper .period.color3 {
  background-color: aquamarine;
}
.time-wrapper .period.color4 {
  background-color: #e69597;
}
.time-wrapper .period.color5 {
  background-color: #a0cedd;
}
.time-wrapper .period.color6 {
  background-color: #b8c5d5;
}
.time-wrapper .period.color7 {
  background-color: #f7e78e;
}
.time-wrapper .period span {
  display: block;
  width: 100%;
}
.time-wrapper .period .subject {
  font-size: 13px;
  font-weight: 500;
  background-color: #BC89E0;
  border-radius: 8px;
  padding: 12px 10px;
  margin-bottom: 0px;
  color: #000 !important;
}
.time-wrapper .period .class {
  font-size: 13px;
  font-weight: normal;
  width: 110px;
  color: #000 !important;
}
.time-wrapper .period .class span {
  display: block;
  width: 100%;
  color: #000 !important;
}
.time-wrapper .period .time {
  font-size: 13px;
  font-weight: normal;
  position: absolute;
  top: 0;
  width: 100%;
  display: flex;
}
.time-wrapper .period .time .left {
  flex: 1;
  white-space: nowrap;
  position: relative;
  top: 8px;
  padding: 0;
  margin-right: 7px;
  color: #ee8013;
}
.time-wrapper .period .time .border-dashed {
  flex: 1 1 auto;
  border-bottom: 1px dashed #F97794;
}

.white-space {
  white-space: nowrap;
  overflow-x: scroll;
  padding: 15px 0;
  padding-top: 0;
  display: flex;
  column-gap: 4px;
}

.month ion-chip {
  display: inline-block;
  --background: #d5d8da;
  padding: 8px 2px;
  min-width: 40px;
}
.month ion-chip ion-label {
  width: 100%;
  text-align: center;
  display: block;
  font-size: 11px;
  color: #000;
}
.month ion-chip ion-label.day {
  font-weight: 500;
  color: #000 !important;
}
.month ion-chip ion-label.date {
  font-weight: 500;
  color: #525151 !important;
}
.month ion-chip.selected {
  --background: linear-gradient(135deg, #da8e63, #c754aa);
}
.month ion-chip.selected ion-label {
  color: #fff !important;
}

.fw-500 {
  font-weight: 500;
}

.date-header {
  padding-left: 15px;
}
.date-header ion-card-subtitle {
  text-transform: initial;
}
.date-header ion-card-subtitle span + span {
  font-weight: 500;
  letter-spacing: 0;
